/* Colors */
@color0: #37474F; /* sidebar background */
@color1: #37363E; /* black */ /* This font also default title color */
@color2: #58666E; /* lighten black */ /* This font also default text color */
@color3: #E4E4E4; /* grey */
@color4: #F5F5F5; /* lighten grey */ 
@color5: #607D8B; /* default */
@color6: #33577B; /* primary */
@color7: #26A65B; /* success */
@color8: #51B7A3; /* info */
@color9: #F39C12; /* warning */
@color10: #EF4836; /* danger */
@color11: #9A80B9; /* purple */
@color12: #A2DED0; /* cs */
@color13: #D2527F; /* cs */
@color14: #C78568; /* cs */
@color15: #E99844; /* cs */


/* ===== Colors ===== */
@topbg: @color5; /* top background also main theme color */
@sidebarbg: @color0; /* sidebar background also page background color */
@contenttitle: @color1; /* main content light text color */
@contenttext: @color2; /* main content text color */

/* Fonts */
@primaryfont: -apple-system, BlinkMacSystemFont, 
    "Segoe UI", 'Open Sans', "Roboto", "Oxygen", 
    "Ubuntu", "Cantarell", "Fira Sans", 
    "Droid Sans", "Helvetica Neue", sans-serif;
@titlefont: 'Montserrat', sans-serif; 

/* Color Class */
.color1{color: @color1;}
.color2{color: @color2;} 
.color3{color: @color3;} 
.color4{color: @color4;} 
.color5{color: @color5;} 
.color6{color: @color6;} 
.color7{color: @color7;}
.color8{color: @color8;} 
.color9{color: @color9;} 
.color10{color: @color10;} 
.color11{color: @color11;} 
.color12{color: @color12;} 
.color13{color: @color13;} 
.color14{color: @color14;} 
.color15{color: @color15;} 

/* Color Class Background */
.color0-bg{background: @color0;}
.color1-bg{background: @color1;}
.color2-bg{background: @color2;}
.color3-bg{background: @color3;}
.color4-bg{background: @color4;}
.color5-bg{background: @color5;}
.color6-bg{background: @color6;}
.color7-bg{background: @color7;}
.color8-bg{background: @color8;}
.color9-bg{background: @color9;}
.color10-bg{background: @color10;}
.color11-bg{background: @color11;}
.color12-bg{background: @color12;}
.color13-bg{background: @color13;}
.color14-bg{background: @color14;}
.color15-bg{background: @color15;}

/* Up - Down */
.color-up{color: @color7;}
.color-down{color: @color10;}
.color-fix{color: @color5;}

body {
    background: @sidebarbg;
    color: @contenttext;
    margin: 0;
    line-height: 1.7em;
    font-size: 13px;
    font-family: @primaryfont;
    outline: 0;
}

::-moz-selection {
    background: #D5EAFF;
}

::selection {
    background: #D5EAFF;
}

a {
    color: @topbg;
    text-decoration: none;
}

a:hover,
a:focus {
    text-decoration: none;
}

b,
strong {
    font-weight: 600;
}


