/* =================================================================================
Top
================================================================================= */

#top {
    height: 60px;
    background: @topbg;
    color: #fff;
    width: 100%;
    position: fixed;
    z-index: 900;
    top: 0;
}


/* =================================================================================
App Logo
================================================================================= */

.applogo {
    width: 250px;
    height: 60px;
    background: rgba(0, 0, 0, 0.14);
    color: #fff;
    position: relative;
    padding: 19px 0 0 0px;
    float: left;
    text-align: center;
    .logo {
        color: #fff;
        font-size: 30px;
        font-weight: bold;
        font-family: @titlefont;
    }
}


/* =================================================================================
Sidebar Open Button - Show or Hide Sidebar
================================================================================= */

.sidebar-open-button {
    height: 60px;
    display: block;
    font-size: 22px;
    display: block;
    width: 62px;
    text-align: center;
    float: left;
    color: rgba(0, 0, 0, 0.6);
    padding-top: 19px;
    border-right: 1px solid rgba(0, 0, 0, 0.14);
    z-index: 1;
}

.sidebar-open-button:hover,
.sidebar-open-button:active {
    background: rgba(255, 255, 255, 0.07);
}

.sidebar-open-button-mobile {
    height: 60px;
    display: none;
    font-size: 22px;
    width: 62px;
    text-align: center;
    float: left;
    color: rgba(0, 0, 0, 0.6);
    padding-top: 19px;
    border-right: 1px solid rgba(0, 0, 0, 0.14);
}

.sidebar-open-button-mobile:hover,
.sidebar-open-button-mobile:active {
    background: rgba(255, 255, 255, 0.07);
}


/* =================================================================================
Searchbox - Top Search Input
================================================================================= */

.searchform {
    width: 220px;
    margin: 0 20px;
    float: left;
    padding-top: 14px;
    position: relative;
}

.searchbox {
    border-radius: 999px;
    border: none;
    height: 32px;
    width: 220px;
    padding-left: 20px;
    padding-right: 36px;
    background: rgba(255, 255, 255, 0.95);
    color: @color1;
    box-shadow: inset none;
}

.searchbox:focus {
    background: #fff;
    box-shadow: none;
    border-top: none;
}

.searchbutton {
    border: none;
    color: rgba(0, 0, 0, 0.5);
    background: none;
    position: absolute;
    top: 18px;
    font-size: 16px;
    right: 15px;
}


/* =================================================================================
Top Menu 
================================================================================= */

.topmenu {
    float: left;
    padding-top: 20px;
    padding-left: 0;
    font-weight: 600;
    margin-left: -7px;
    a {
        color: rgba(255, 255, 255, 0.9);
        padding: 7px;
    }
    a:hover {
        color: #fff;
    }
    li {
        display: inline;
    }
}


/* =================================================================================
Sidepanel Open Button - Show or Hide Sidepanel
================================================================================= */

.sidepanel-open-button {
    height: 60px;
    font-size: 22px;
    display: block;
    width: 62px;
    text-align: center;
    float: right;
    color: rgba(0, 0, 0, 0.6);
    padding-top: 19px;
    border-left: 1px solid rgba(0, 0, 0, 0.14);
    z-index: 1;
}

.sidepanel-open-button:hover,
.sidepanel-open-button:active {
    background: rgba(255, 255, 255, 0.05);
}


/* =================================================================================
Top Right
================================================================================= */

.top-right {
    line-height: 1.8em;
    float: right;
    padding-right: 20px;
    padding-left: 10px;
    padding-top: 15px;
    font-weight: 600;
    .link {
        display: inline-block;
        padding-left: 8px;
        height: 30px;
        a {
            display: block;
        }
    }
    .dropdown-menu {
        min-width: 135px;
        .list-title {
            text-align: center;
        }
        li {
            position: relative;
            display: block;
        }
    }
    a {
        color: #fff;
    }
    /* profile box */
    .profilebox {
        color: rgba(255, 255, 255, 0.95);
        img {
            width: 32px;
            height: 31px;
            float: left;
            border-radius: 100%;
            margin-right: 7px;
            margin-top: -4px;
        }
        .caret {
            margin-left: 5px;
            color: rgba(255, 255, 255, 0.5);
        }
    }
    .profilebox:hover {
        color: #fff;
    }
    /* notifications */
    .notifications {
        color: #fff;
        padding: 4px 12px;
        font-weight: bold;
        background: rgba(0, 0, 0, 0.4);
        border-radius: 999px;
    }
    .notifications:hover {
        background: rgba(0, 0, 0, 0.5);
    }
    /* hd button */
    .hdbutton {
        background: #fff;
        color: #404B5F;
        padding: 4px 16px;
        border-radius: 999px;
    }
    .hdbutton:hover {
        background: rgba(255, 255, 255, 0.95);
    }
}


/* =================================================================================
Sidebar 
================================================================================= */

.sidebar {
    width: 250px;
    position: absolute;
    top: 60px;
    left: 0;
    font-family: @titlefont;
    background: @color0;
    z-index: 0;
    font-size: 13px;
    font-weight: normal;
    z-index: 500;
    color: rgba(255, 255, 255, 0.8);
    .label {
        font-size: 10px;
        position: relative;
        line-height: 12px;
        padding: 2px 5px;
        position: absolute;
        right: 35px;
        top: 12px;
        font-weight: 600;
    }
    .label-default {
        background: rgba(255, 255, 255, 0.2);
    }
}

.sidebar-panel {
    background: none;
    margin: 0px 20px 0px 20px;
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 15px 0px;
    li {
        a {
            display: block;
            color: rgba(255, 255, 255, 0.6);
            padding: 9px 20px 9px 54px;
            margin-left: -20px;
            margin-right: -20px;
            position: relative;
            .caret {
                position: absolute;
                right: 20px;
                top: 18px;
                color: rgba(255, 255, 255, 0.4);
            }
        }
        .icon {
            position: absolute;
            left: 20px;
            top: 8px;
            display: block;
            color: rgba(255, 255, 255, 0.6);
            font-size: 19px;
            border-radius: 3px;
            text-align: center;
            width: 24px;
            height: 24px;
            padding-top: 1px;
        }
        a:hover,
        a.active {
            color: #f2f2f2;
            background: rgba(0, 0, 0, 0.2);
            .icon {
                color: #fff;
            }
        }
        ul {
            display: none;
            padding: 0;
            background: rgba(0, 0, 0, 0.1);
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            font-size: 13px;
            margin: 0 -20px 0 -20px;
            font-weight: 400;
            li {
                position: relative;
                margin: 0;
                a {
                    margin: 0px;
                    color: rgba(255, 255, 255, 0.6);
                    padding: 9px 20px 9px 54px;
                }
                a:hover {
                    background: rgba(0, 0, 0, 0.08);
                }
            }
        }
    }
}

.sidetitle {
    color: rgba(255, 255, 255, 0.5);
    font-size: 10px;
    padding: 0;
    margin: 0 0 5px 3px;
    letter-spacing: 0.4px;
}

.sidebar-plan {
    padding: 20px;
    font-size: 12px;
    bottom: 0;
    .progress {
        height: 6px;
        background: rgba(0, 0, 0, 0.3);
        margin: 7px 0;
    }
    .progress-bar {
        background: @topbg;
    }
    .link {
        position: absolute;
        right: 20px;
        color: rgba(255, 255, 255, 0.6);
    }
    .link:hover {
        color: #fff;
    }
    .space {
        display: block;
        font-size: 11px;
        text-align: center;
        color: rgba(255, 255, 255, 0.5);
    }
}

.sidebar-colorful {
    .sidebar-panel {
        li {
            a {
                .icon {
                    font-size: 15px;
                    color: #fff;
                }
            }
        }
    }
    .color1 {
        background: @color5;
    }
    .color2 {
        background: @color6;
    }
    .color3 {
        background: @color7;
    }
    .color4 {
        background: @color8;
    }
    .color5 {
        background: @color9;
    }
    .color6 {
        background: @color10;
    }
    .color7 {
        background: @color11;
    }
    .color8 {
        background: @color12;
    }
    .color9 {
        background: @color13;
    }
    .color10 {
        background: @color14;
    }
    .color11 {
        background: @color15;
    }
}

.sidebar-square {
    .sidebar-panel {
        li {
            a {
                .icon {
                    font-size: 14px;
                    border-radius: 0;
                    background: rgba(0, 0, 0, 0.3);
                }
            }
        }
    }
}

.sidebar-no-icon {
    .sidebar-panel {
        li {
            a {
                padding-left: 23px;
                .icon {
                    display: none;
                }
            }
        }
    }
}


/* =================================================================================
Sidepanel 
================================================================================= */

.sidepanel {
    z-index: 9999;
    position: fixed;
    right: 0;
    display: none;
    width: 300px;
    top: 60px;
    bottom: 0;
    right: 0;
    padding-top: 20px;
    overflow: auto;
    box-shadow: -1px 0px 1px rgba(0, 0, 0, 0.2);
    background: #FBFBFD;
    ul {
        padding: 0;
    }
    li {
        display: block;
    }
    li a:hover {
        background: rgba(0, 0, 0, 0.02);
    }
    .nav-tabs {
        font-size: 12px;
        font-weight: bold;
        display: block;
        width: 300px;
        position: fixed;
        background: @color0;
        z-index: 2;
        top: 60px;
        padding: 0;
    }
    .nav-tabs > li {
        float: left;
        margin-bottom: -1px;
        display: inline-block;
        border-right: none;
        width: 33.3333%;
        margin: 0;
    }
    .nav-tabs > li > a {
        margin-right: 0px;
        line-height: normal;
        color: #fff;
        display: block;
        padding: 10px 0;
        border: none;
        border-radius: 0px;
        text-align: center;
        margin-bottom: -1px;
        padding: 12px 0;
    }
    .nav-tabs > li > a:hover {
        border-color: none;
    }
    .nav-tabs > li.active > a,
    .nav-tabs > li.active > a:hover,
    .nav-tabs > li.active > a:focus {
        color: #399BFF;
        cursor: default;
        background-color: #fff;
        border: none;
    }
}

.sidepanel-m-title {
    text-align: center;
    font-family: @titlefont;
    font-size: 14px;
    color: @color1;
    padding: 16px 0;
    position: relative;
    .left-icon,
    .right-icon {
        border: 1px solid #e1e1e1;
        color: @color2;
        position: absolute;
        width: 26px;
        font-size: 13px;
        height: 26px;
        border-radius: 3px;
        a {
            color: @color2;
            display: block;
        }
        a:hover {
            opacity: 0.7;
        }
    }
    .left-icon {
        left: 0;
    }
    .right-icon {
        right: 0;
    }
}


/* Today */

.today {
    padding: 0;
}


/* List with Title */

.list-w-title {
    .label {
        font-family: @titlefont;
        font-size: 11px;
    }
    .date {
        position: absolute;
        right: 20px;
        font-size: 11px;
        color: #76757B;
        top: 23px;
    }
    margin: 0px -20px;
    a {
        display: block;
        position: relative;
        padding: 20px;
        border-bottom: 1px solid #E5E5E5;
        color: #7A8189;
    }
    h4 {
        margin: 5px 0;
        font-size: 13px;
        font-family: @titlefont;
    }
}


/* General Sidepanel Title */

.gn-title {
    background: #E4E4E4;
    font-family: @titlefont;
    font-size: 12px;
    margin: 0 -20px;
    padding: 4px 0 4px 20px;
}


/* Todo List */

.todo-list {
    li {
        padding: 5px 0 5px 20px;
    }
}


/* Chat List */

#chat {
    position: relative;
    .group {
        font-size: 11px;
        margin: 0px -20px;
        .member {
            position: relative;
            line-height: 17px;
            a {
                display: block;
                padding: 10px 20px;
                color: #7A8893;
            }
            a:hover {
                color: #3D464C;
            }
            img {
                border-radius: 50%;
                height: 36px;
                width: 36px;
                float: left;
                margin-right: 10px;
                margin-top: 0px;
            }
            b {
                font-weight: 600;
                display: block;
                font-size: 13px;
            }
            .status {
                display: block;
                position: absolute;
                right: 20px;
                top: 50%;
                bottom: 50%;
                margin: -4px 0;
                width: 8px;
                height: 8px;
                border-radius: 50%;
            }
            .online {
                background: #3CA63F;
            }
            .busy {
                background: #EA5944;
            }
            .away {
                background: #E99844;
            }
            .offline {
                background: #7B8994;
            }
        }
    }
    .search {
        padding: 20px;
        border-top: 1px solid #E5E5E5;
        background: #fff;
        position: fixed;
        bottom: 0;
        width: 300px;
        margin-bottom: 0;
        margin-left: -20px;
    }
}


/* =================================================================================
Content 
================================================================================= */

.content {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 80px;
    margin-left: 250px;
    background: @color4;
}


/* =================================================================================
FOOTER 
================================================================================= */

.footer {
    border-top: 1px solid #e2e2e2;
    margin-left: -30px;
    margin-right: -30px;
    margin-bottom: 0;
    padding: 10px;
    font-size: 11px;
    color: #666;
    background: rgba(0, 0, 0, 0.02);
    .col-md-6 {
        margin-bottom: 0;
    }
}


/* =================================================================================
Page Header 
================================================================================= */

.page-header {
    background: #fff;
    margin: -20px -30px 20px -30px;
    padding: 20px;
    border-bottom: none;
    position: relative;
    .title {
        padding: 0;
        margin: 0;
        font-family: @titlefont;
        font-size: 20px;
        line-height: normal;
        font-weight: normal;
        color: @contenttitle;
        padding-bottom: 6px;
    }
    .right {
        position: absolute;
        right: 20px;
        bottom: 20px;
        min-width: 500px;
        text-align: right;
        .btn-group {
            float: right;
        }
        .btn {
            padding-left: 14px;
            padding-right: 14px;
            font-size: 13px;
            .fa {
                margin: 0 4px;
            }
            .no-border {
                border: none;
            }
        }
        .btn-group {
            .btn {
                border: 1px solid #E8EBED;
            }
        }
    }
    .pagination {
        margin: 0;
    }
    .widget-inline-list {
        right: 0;
        bottom: -25px;
        position: relative !important;
    }
}


/* Page loading */

.loading {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: @color0;
    opacity: 0.99;
    img {
        width: 40px;
        height: 40px;
        position: absolute;
        left: 50%;
        right: 50%;
        bottom: 50%;
        top: 50%;
        margin: -20px;
    }
}


/* =================================================================================
Presentation 
================================================================================= */

.presentation {
    background: rgba(0, 0, 0, 0.05);
    margin: -20px -30px 30px -30px;
    padding: 70px 50px;
    .titles {
        .icon {
            color: #fff;
            border-radius: 3px;
            font-size: 36px;
            line-height: 0;
            padding: 8px 16px;
            text-align: center;
            .fa {
                font-weight: inherit;
            }
        }
        h1 {
            font-size: 38px;
            font-weight: 600;
            font-family: @titlefont;
