/* Kode Alert */

.kode-alert {
    color: #fff;
    position: relative;
    border-radius: 3px;
    text-align: left;
    margin-bottom: 10px;
    padding: 12px;
    padding-right: 30px;
}

.kode-alert a {
    color: inherit;
    text-decoration: underline;
    font-weight: 600;
}

.kode-alert h4 {
    font-size: 14px;
    margin: 0;
    color: inherit;
    font-weight: 600;
    line-height: normal;
}

.kode-alert .img {
    width: 40px;
    height: 40px;
    position: absolute;
    border-radius: 3px;
    left: 12px;
    top: 12px;
}

.kode-alert-img {
    padding-left: 65px;
    min-height: 64px;
}

.kode-alert-icon {
    padding-left: 40px;
    .fa {
        display: block;
        width: 20px;
        text-align: center;
        position: absolute;
        font-size: 15px;
        left: 12px;
        top: 17px;
    }
}

.kode-alert .closed {
    position: absolute;
    right: 3px;
    text-decoration: none;
    font-weight: bold;
    top: 0px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.5);
    padding: 4px;
}

.kode-alert .closed:hover {
    color: #fff;
}

.kode-alert-click {
    cursor: pointer;
    padding-right: 12px;
}

.kode-alert .primary {
    outline: 0;
    border: none;
    background: rgba(0, 0, 0, 0.4);
    color: inherit;
    border-radius: 3px;
    padding: 4px 10px;
}

.kode-alert .cancel {
    outline: 0;
    border: none;
    background: rgba(255, 255, 255, 0.4);
    color: rgba(0, 0, 0, 0.8);
    border-radius: 3px;
    padding: 4px 10px;
}

.kode-alert .primary:hover,
.kode-alert .cancel:hover {
    opacity: 0.9;
}

.kode-alert-top,
.kode-alert-bottom,
.kode-alert-top-left,
.kode-alert-top-right,
.kode-alert-bottom-left,
.kode-alert-bottom-right,
.kode-alert-fullscreen {
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
    position: fixed;
    display: none;
    z-index: 1000;
}

.kode-alert-top {
    top: 0;
    left: 0;
    right: 0;
}

.kode-alert-bottom {
    bottom: 0;
    left: 0;
    right: 0;
}

.kode-alert-top-left {
    top: 80px;
    left: 20px;
}

.kode-alert-top-right {
    top: 80px;
    right: 20px;
}

.kode-alert-bottom-left {
    bottom: 20px;
    left: 20px;
}

.kode-alert-bottom-right {
    bottom: 20px;
    right: 20px;
}

.kode-alert-fullsize {
    top: 50%;
    left: 50%;
    margin: -20px;
}

