    }
    li:last-child {
        border-bottom: none;
    }
    .right {
        position: absolute;
        right: 0;
        display: inline-block;
        padding: 3px 6px;
        font-weight: 600;
    }
}


/* Image List */

.image-list {
    li {
        padding-left: 50px;
    }
    .img {
        width: 40px;
        height: 40px;
        position: absolute;
        left: 0;
        border-radius: 999px;
    }
    .desc {
        font-size: 12px;
        opacity: 0.8;
        display: block;
    }
}


/* Blog Post */

.blog-post {
    .image-div {
        border-radius: 3px;
        margin-bottom: 14px;
        position: relative;
        overflow: hidden;
        a {
            color: #fff;
        }
        a:hover {
            opacity: 0.8;
        }
    }
    .image {
        width: 100%;
        min-height: 100px;
        border-radius: 3px;
        top: 0;
        opacity: 0.2;
    }
    .title {
        position: absolute;
        top: 0;
        color: #fff;
        padding: 10px;
        margin: 0;
        line-height: normal;
        font-size: 34px;
        font-weight: 300;
    }
    .author {
        position: relative;
        padding-left: 50px;
        border-top: 1px solid #ededed;
        padding-top: 20px;
        margin-top: 15px;
        margin-bottom: 0;
        img {
            width: 40px;
            height: 40px;
            border-radius: 999px;
            position: absolute;
            left: 0;
        }
        .text {
            margin-bottom: 20px;
        }
        span {
            display: block;
            color: @color1;
            font-weight: 600;
        }
    }
}


/* Quick Menu */

.quick-menu {
    padding: 0;
    background: #fff;
    margin-bottom: 10px;
    border-radius: 3px;
    font-family: @titlefont;
    .label {
        position: absolute;
        padding: 4px 6px;
        top: -10px;
        right: 10px;
    }
    li {
        display: block;
        text-align: center;
        padding: 0;
        position: relative;
        font-size: 13px;
        .fa {
            display: block;
            font-size: 28px;
            margin-bottom: 5px;
            opacity: 0.8;
        }
    }
    a {
        color: @color2;
        display: block;
        padding: 18px 0;
        .fa {
            color: @color5;
        }
    }
    a:hover {
        background: rgba(0, 0, 0, .03);
        color: @color1;
    }
}


/* Widget Map */

.gmaps,
.gmaps-panaroma {
    height: 300px;
    background: #6699cc;
    border-radius: 3px;
}

.gmaps-overlay {
    display: block;
    text-align: center;
    color: #fff;
    font-size: 20px;
    line-height: 40px;
    opacity: 0.8;
    background: #4477aa;
    border: solid 3px #336699;
    border-radius: 4px;
    box-shadow: 2px 2px 10px #333;
    text-shadow: 1px 1px 1px #666;
    padding: 0 4px;
}

.gmaps-overlay_arrow {
    left: 50%;
    margin-left: -16px;
    width: 0;
    height: 0;
    position: absolute;
}

.gmaps-overlay_arrow.above {
    bottom: -15px;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    border-top: 16px solid #336699;
}

.gmaps-overlay_arrow.below {
    top: -15px;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    border-bottom: 16px solid #336699;
}


/* widget table-dic */

.table-dic {
    .fa {
        text-shadow: none;
        color: @color5;
        font-size: 20px;
        margin-right: 8px;
    }
    .type {
        font-size: 11px;
        opacity: 0.7;
    }
}

